<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGround">
      <slot>
      </slot>
    </div>
    <div class="dots">
        <span class="dot" v-for="(item,index) in dots" :class="{active:currentPageIndex===index}"></span>
    </div>
  </div>
</template>


<!--props:他可以从外部控制这个组件，控制轮播图有那些类型或属性，譬如轮播间隔，循环轮播；-->
<!--注意:在移动端的时候，点击不能跳转，是因为fastclick跟better-scroll冲突了，删除掉click:true即可解决-->
<script type="text/ecmascript-6">

    import BScroll from 'better-scroll'
    import {addClass} from 'common/js/dom'

     export default {
         data() {
             return {
                 dots:[],
                 currentPageIndex:0
             }
         },
         props: {  
             loop: {
                 type: Boolean,
                 default: true
             },
             autoPlay: {
                 type: Boolean,
                 default: true
             },
             interval: {
                 type: Number,
                 default: 2000
             }
         },
         mounted() {
             setTimeout(() => {
                 this._setSliderWidth();
                 this._initDots();
                 this._initSlider();

                 if(this.autoPlay){
                     this.play();
                 }
             },20)

             window.addEventListener('resize', () => {
                 if(!this.slider) {
                     return;
                 }
                 this._setSliderWidth(true);
                 this.slider.refresh();
             })
         },
         methods: {
             _setSliderWidth(isResize) {                
                 this.children = this.$refs.sliderGround.children;
                 let width = 0;
                 let sliderWidth = this.$refs.slider.clientWidth;
                 for(let i = 0; i <this.children.length; i++){
                     let child = this.children[i];
                     addClass(child,'slider-item');

                     child.style.width = sliderWidth+"px";
                     width += sliderWidth;
                 }
                 if(this.loop && !isResize){
                     width += 2*sliderWidth;
                 }
                 this.$refs.sliderGround.style.width = width + "px";
             },
             _initDots() {
                 this.dots=new Array(this.children.length)
             },
             _initSlider() {
                 this.slider = new BScroll(this.$refs.slider, {
                      scrollX: true,
                      scrollY: false,
                      momentum: false,
                      snap: true,
                      snapLoop: this.loop,
                      snapThreshold: 0.3,
                      snapSpeed: 400,
                    //   click:true
                })
                this.slider.on("scrollEnd" ,() => {
                    let pageIndex = this.slider.getCurrentPage().pageX;
                    if(this.loop) {
                        pageIndex -= 1;
                    }
                    this.currentPageIndex = pageIndex;
                    if(this.autoPlay){
                        clearTimeout(this.timer);
                        this.play();
                    }
                })
             },
             play() {
                 let pageIndex = this.currentPageIndex + 1;
                 if(this.loop){
                     pageIndex += 1;
                 }
                 this.timer = setTimeout(() => {
                     this.slider.goToPage(pageIndex, 0, 400)
                 },this.interval)
             }
         }
     }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>


