import 'babel-polyfill'  //引用es6的api 如promise
import Vue from 'vue'
import App from './App'
import router from './router'   //index.js中export出来的router实例，传递到new Vue({})的对象上
import fastclick from 'fastclick'

import 'common/stylus/index.styl'

fastclick.attach(document.body) //解決300毫秒延遲

// Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({ 
    el: '#app', 
    render: h => h(App),
    router
 })


